.cover {
    align-items: center;
    background-image: linear-gradient(to right, #51183d , #261145);
    color: #fff;
    display: flex;
    flex-flow: column;
    height: 48em;
    padding: 13em 3em 3em 3em;
    width: 100%;
    .logo {
        width: 50%; }
    .date {
        color: #e152b5;
        font-size: 1.8em;
        font-weight: 100;
        letter-spacing: .1em;
        margin: 1.5em 0; }
    .open {
        font-size: 1.8em;
        font-style: italic;
        font-weight: 400;
        letter-spacing: .1em; }
    hr {
        display: block;
        border: 1em solid #ee5a30;
        margin-top: 2em;
        margin-bottom: 2em;
        margin-left: auto;
        margin-right: auto;
        border-style: inset;
        border-width: 1px;
        width: 2em; }
    .btn-arrow {
        border: solid .1em #ee5a30;
        border-radius: 60%;
        font-size: 1.6em;
        padding: .8em; } }
